.App-animation-shake{
  animation: shake 0.6s cubic-bezier(.36,.07,.19,.97) both;
  animation-iteration-count: infinite;
}

.App-animation-simmer{
  animation: simmer 0.7s cubic-bezier(.36,.07,.19,.97) both;
  animation-iteration-count: infinite;
}

.App-animation-mixing{
  animation: mixing 1s cubic-bezier(.36,.07,.19,.97) both;
  animation-iteration-count: infinite;
}

@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}

@keyframes simmer {
  10%, 90% {
    transform: translate3d(-1px, 0, 0) rotate(3deg);
  }

  20%, 80% {
    transform: translate3d(2px, 0, 0) rotate(-3deg);
  }

  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0) rotate(3deg);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0) rotate(-3deg);
  }
}

@keyframes mixing {
  10%, 90% {
    transform: translate3d(-5px, 0, 0) rotate(10deg);
  }

  20%, 80% {
    transform: translate3d(6px, 0, 0) rotate(-10deg);
  }

  30%, 50%, 70% {
    transform: translate3d(-8px, 0, 0) rotate(10deg);
  }

  40%, 60% {
    transform: translate3d(8px, 0, 0) rotate(-10deg);
  }
}

